const domain = 'http://127.0.0.1:3000';
export const register_user = async(data) => {
	let response = await fetch(domain+'/signup', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify(data) })

	response = await response.json()

	return response
}

export const login_user = async (data)=>{
	console.log(domain);
	try{
	let response = await fetch(domain+'/signin', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify(data) });
	response = await response.json()
	return response
	}catch(e){
		console.log(e);
		return {success:'',type:'error'}  
	}
}

export const check_name = async(name) => {
	let response = await fetch(domain+'/check_name', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify({ 'name':name }) })

	response = await response.json()

	return response
}

export const check_number = async(number) => {
	let response = await fetch(domain+'/check_number', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify({ number }) })

	response = await response.json()

	return response
}

export const check_mail = async(email) => {
	let response = await fetch(domain+'/check_email', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify({ email }) })

	response = await response.json()

	return response
}
export const verify = async(email) => {
	try{
	let token = localStorage.getItem('CSRF_token');
	let response = await fetch(domain+'/verify', { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify({ token:token }) })

	response = await response.json()

	return response
}catch(e){
	console.log(e);
	return {success:''}
}
};